@import "_normalize";
@import "_variables";

@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap");

@keyframes backgroundChange {
  0% {
    background: #eeeeee;
    transform: scale(1);
  }
  25% {
    background: #9ad2cb;
    transform: scale(1);
  }
  50% {
    background: #f4e285;
    transform: scale(1);
  }
  75% {
    background: #8cb369;
    transform: scale(1);
  }
  100% {
    background: #eeeeee;
    transform: scale(1);
  }
}

html {
  box-sizing: border-box;
  font-family: "Open Sans";
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  color: #000000;
  background: #eeeeee;
  display: flex;
  padding: 16px;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  animation: backgroundChange 10s infinite;
  @media (max-width: 320px) {
    min-width: 260px;
  }
}

.main {
  display: flex;
  position: relative;
  width: 100%;
  flex-direction: column;
  max-width: 400px;
}

.header {
  font-size: 32px;
  font-weight: 600;
  margin: 32px 0 16px;
}

.form {
  &__actions {
    display: flex;
    justify-content: space-between;
    margin-top: 24px;
  }
  &__button {
    background: #fff;
    border: 1px solid #121232;
    padding: 12px 16px;
    border-radius: 8px;
    font-weight: 600;
    min-width: 79px;
    transition: transform 0.3s ease-in-out;
    &:hover {
      transform: scale(1.05);
    }
    &--save {
      background: #121232;
      color: #ffffff;
    }
    &--load {
      background: #fff;
      color: #000;
      margin-right: 42px;
    }
  }
  &__label {
    display: block;
    margin-bottom: 8px;
    font-size: 14px;
  }
  &__message {
    background: #ffffff;
    padding: 12px;
    width: 100%;
    border-radius: 8px;
    min-height: 200px;
  }
}

.position {
  margin: 0 auto;
}

g.scale {
  transition: transform 0.3s ease-in-out;
  &:hover {
    transform: scale(1.02);
  }
}
